import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

export default {
  state: {
    regError: ''
  },
  mutations: {
    regError(state, error) {
      state.regError = error
    }
  },
  actions: {
    async register(context, {email, password, name}) {
      try {
        await firebase.auth().createUserWithEmailAndPassword(email, password);
        let uid = await context.dispatch('getUid');
        await firebase.database().ref(`/users/${uid}/info`).set({name});
        await context.commit('closer');
        await context.commit('formSwitch', 'auth');
        await context.commit('regError', '')
      }
      catch(error) {
        context.commit('regError', error.message)
      }
    },
    getUid() {
      let user = firebase.auth().currentUser
      return user ? user.uid : null
    }
  }    
}