import Vue from 'vue'
import VueRouter from 'vue-router'
import Homepage from '../views/homepage'

Vue.use(VueRouter)

  const routes = [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/mainpage',
      name: 'Mainpage',
      component: () => import('../views/mainpage.vue')
    }
  ]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
