import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

export default {
  state: {
    user: 'Гость',
    authError: ''
  },
  mutations: {
    changeUser(state, userName) {
      state.user = userName;
    },
    authError(state, error) {
      state.authError = error
    }
  },
  actions: {
    async changeUser(context, {email, password}) {
      try {
        await firebase.auth().signInWithEmailAndPassword(email, password);
        await context.commit('closer');
        await context.dispatch('getUserName');
        await context.commit('formSwitch', 'logout');
        await context.commit('authError', '')
      }
      catch(error) {
        context.commit('authError', error.message)
      }
    },
    async getUserName(context) {
      let userID = firebase.auth().currentUser.uid;
      let userName = (await firebase.database().ref(`/users/${userID}/info`).once('value')).val();
      context.commit('changeUser', userName.name)
    }
  }
}