import firebase from 'firebase/app'
import 'firebase/auth'
import 'firebase/database'

export default {
  state: {
    cart: [],
    cartIsClosed: false,
    forGuests: false,
    orderIsDone: false,
    notAuthorized: false,
    emptyCart: false
  },
  mutations: {
    addToCart(state, payload) {
      if (state.cart.includes(payload)) {
        return;
      }
      state.cart.push(payload)
    },
    cartCloser(state) {
      state.cartIsClosed = false,
      state.orderIsDone = false,
      state.notAuthorized = false,
      state.emptyCart = false,
      state.cart = []
    },
    delete(state, payload) {
      let index = state.cart.indexOf(payload);
      state.cart.splice(index, 1)
    },
    closeForGuests(state) {
      state.forGuests = !state.forGuests
    },
    orderIsDoneChanger(state) {
      state.orderIsDone = true
    },
    notAuthorizedChanger(state) {
      state.notAuthorized = true
    },
    emptyCartChanger(state) {
      state.emptyCart = true
    }
  },
  actions: {
    getCurrentUser(context, order) {
      let user = firebase.auth().currentUser;
      firebase.database().ref(`/users/${user.uid}/order`).set({order});
    }
  }
}